import React from "react";
import {Field, reduxForm} from "redux-form";
import {CreateField, Input} from "../form_controls/form_controls";
import {max_letngtcreator, required} from "../utils/validators/validators";
import {connect} from "react-redux";
import mapDispatchToProps from "react-redux/lib/connect/mapDispatchToProps";
import {Login_T} from "../../redux/Auth_reduser";
import {Redirect} from "react-router-dom";
import styles from '../form_controls/form_control.module.css'

const maxLength100 = max_letngtcreator(100)
const maxLength12 = max_letngtcreator(12)

const Loginform = ({handleSubmit, error,captcha}) => {

    return <div>
        <form onSubmit={handleSubmit}>
            {CreateField( 'login_email', 'login_email',[required, maxLength100],Input,)}
            {CreateField( 'password', 'password',[required, maxLength12],Input, 'password')}
            {CreateField( 'rememberMe', null,null,Input, 'checkbox',null,'remember me')}

            {captcha && <img src={captcha} alt="captcha"/>}
            {captcha && CreateField( 'captcha', "put captcha",[required],Input,)}


            {error && <div className={styles.formSumError}><h4> {error}</h4></div>}
            <div>
                <button> go</button>
            </div>
        </form>
    </div>
}
const Login_redux_form = reduxForm({form: 'login',})(Loginform)

const Login_conteyner1 = ({Login_T,isOuth,captcha}) => {
    const onSubmit = (formData2) => {
        Login_T(formData2.login_email, formData2.password, formData2.rememberMe,formData2.captcha)
    }
    if(!isOuth){
    return <div>login
        <h1>form</h1>
        <Login_redux_form onSubmit={onSubmit} captcha={captcha}/>
    </div>
    }
    return <Redirect to={'Profile_page'} />
}
const mapStateToProps = (state)=>{
    return {
    isOuth: state.auth.isOuth,
        captcha: state.auth.captcha
}}
const Login_conteyner = connect(mapStateToProps, {Login_T})(Login_conteyner1)
export default Login_conteyner











